const BASE_URL = 'http://localhost';
// exportmyRequest,myRequestoptions
export const myRequest = (options)=>{  
    return new Promise((resolve,reject)=>{  // promisereturn
        uni.request({  // 
            url: BASE_URL + options.url,  // options
            method: options.method || 'GET', // optionsmethodGET
            data: options.data || {},  // dataoptionsdata 
            success: (res) => {  // res
			   const result = JSON.parse(JSON.stringify(res));
				// console.log(res);
			     var res1 = JSON.parse(JSON.stringify(res.data));
				 // console.log(res1);
				 // console.log(res1.data.status);
                if(res1.data.status !== 0){  // 
                    return uni.showToast({
                        title: ''
                    })
                }
                resolve(res1)  //resolve
            },
            fail: (err)=>{ // err 
                uni.showToast({  // 
                    title: ''
                })
                reject(err) // reject
            }
            
        })
    })
}