export default {

	// 
	jump(url, type = 1) {
		// 
		if (type == 1) {
			uni.navigateTo({
				url: url
			})
		}

		// 
		if (type == 2) {
			uni.redirectTo({
				url: url
			})
		}
		// 
		if (type == 3) {
			uni.reLaunch({
				url: url
			})
		}

		//  tabBar  tabBar 
		if (type == 4) {
			uni.switchTab({
				url: url
			})
		}
	},
	tostring(value){
		return JSON.stringify(value);
	}
}
